"use client";

import { useState, useEffect } from "react";
import { Card } from "@/components/ui/card";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

interface MenuSettings {
  position: string;
  binding: string;
  customX?: number;
  customY?: number;
}

interface MenuSettingsProps {
  onSettingsChange: (settings: MenuSettings) => void;
  defaultSettings?: MenuSettings;
}

const positions = [
  "Top-Left",
  "Top-Center",
  "Top-Right",
  "Center-Left",
  "Center",
  "Center-Right",
  "Bottom-Left",
  "Bottom-Center",
  "Bottom-Right",
  "Custom",
];

const bindings = [
  "PS4_Share",
  "PS4_Options",
  "Xbox_Menu",
  "Custom",
];

export function MenuSettings({ onSettingsChange, defaultSettings }: MenuSettingsProps) {
  const [position, setPosition] = useState<string>(defaultSettings?.position || "Center");
  const [binding, setBinding] = useState<string>(defaultSettings?.binding || "PS4_Share");
  const [customX, setCustomX] = useState<number | undefined>(defaultSettings?.customX);
  const [customY, setCustomY] = useState<number | undefined>(defaultSettings?.customY);

  useEffect(() => {
    if (position !== "Custom") {
      onSettingsChange({ position, binding });
    } else {
      onSettingsChange({ position, binding, customX, customY });
    }
  }, [position, binding, customX, customY, onSettingsChange]);

  return (
    <Card className="p-4 mb-6">
      <h3 className="text-lg font-semibold mb-4 text-white">Menu Settings</h3>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 items-center">
        <div>
          <Label htmlFor="position-select" className="text-white">Menu Position</Label>
          <Select value={position} onValueChange={setPosition}>
            <SelectTrigger className="w-full">
              <SelectValue placeholder="Select position" />
            </SelectTrigger>
            <SelectContent>
              {positions.map((pos) => (
                <SelectItem key={pos} value={pos}>{pos}</SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        {position === "Custom" && (
          <>
            <div>
              <Label htmlFor="custom-x" className="text-white">Custom X</Label>
              <Input
                type="number"
                id="custom-x"
                value={customX ?? ""}
                onChange={(e) => setCustomX(Number(e.target.value))}
                placeholder="X coordinate"
                className="text-white bg-zinc-800"
              />
            </div>
            <div>
              <Label htmlFor="custom-y" className="text-white">Custom Y</Label>
              <Input
                type="number"
                id="custom-y"
                value={customY ?? ""}
                onChange={(e) => setCustomY(Number(e.target.value))}
                placeholder="Y coordinate"
                className="text-white bg-zinc-800"
              />
            </div>
          </>
        )}

        <div>
          <Label htmlFor="binding-select" className="text-white">Open Menu Binding</Label>
          <Select value={binding} onValueChange={setBinding}>
            <SelectTrigger className="w-full">
              <SelectValue placeholder="Select binding" />
            </SelectTrigger>
            <SelectContent>
              {bindings.map((bind) => (
                <SelectItem key={bind} value={bind}>{bind}</SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>

      <div className="mt-6">
        <h4 className="text-white mb-2">OLED Screen Preview</h4>
        <div className="w-48 h-24 bg-black border border-zinc-700 rounded-md relative overflow-hidden">
          {/* Placeholder for OLED preview */}
          <div className="absolute text-white text-xs p-1">
            Position: {position === "Custom" ? `Custom (${customX ?? "?"}, ${customY ?? "?"})` : position}
          </div>
          <div className="absolute bottom-1 right-1 text-white text-xs">
            Binding: {binding}
          </div>
        </div>
      </div>
    </Card>
  );
}
