"use client";

import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";

const shortcuts = [
  { key: "Ctrl + N", description: "Add new menu item" },
  { key: "Ctrl + G", description: "Generate script" },
  { key: "Ctrl + S", description: "Download script" },
  { key: "Ctrl + Del", description: "Delete selected menu item" },
  { key: "Tab", description: "Navigate between menu items" },
  { key: "Enter", description: "Edit menu item name" },
  { key: "Esc", description: "Cancel editing" },
];

export function ShortcutGuide() {
  return (
    <Dialog>
      <DialogTrigger asChild>
        <Button variant="outline" size="sm">
          Keyboard Shortcuts
        </Button>
      </DialogTrigger>
      <DialogContent className="max-w-md">
        <DialogHeader>
          <DialogTitle>Keyboard Shortcuts</DialogTitle>
          <DialogDescription>
            Use these keyboard shortcuts to work more efficiently
          </DialogDescription>
        </DialogHeader>
        <ScrollArea className="h-[300px] rounded-md border p-4">
          <div className="space-y-4">
            {shortcuts.map((shortcut) => (
              <div
                key={shortcut.key}
                className="flex items-center justify-between border-b border-zinc-800 pb-2"
              >
                <span className="font-mono text-sm">{shortcut.key}</span>
                <span className="text-sm text-zinc-400">{shortcut.description}</span>
              </div>
            ))}
          </div>
        </ScrollArea>
      </DialogContent>
    </Dialog>
  );
}
