interface MenuItem {
  id: string;
  name: string;
  type: "menu" | "submenu";
  children?: MenuItem[];
}

interface MenuSettings {
  position: string;
  binding: string;
  customX?: number;
  customY?: number;
}

interface EnhancementSettings {
  verticalRecoil: number;
  horizontalRecoil: number;
  aimEnhancerMode: string;
}

export function generateCronusZenScript(
  menuItems: MenuItem[],
  menuSettings?: MenuSettings,
  enhancementSettings?: EnhancementSettings
): string {
  const timestamp = new Date().toISOString();
  let script = `// Cronus Zen Script
// Generated with Cronus Zen Script Designer
// Generated at: ${timestamp}
// Version: 1.0.0

MAIN {
  // Main script initialization
  init();
  
  // Menu system setup
  setupMenus();
  
  // Main loop
  while(true) {
    handleMenus();
    wait(1);
  }
}

void init() {
  // Initialize variables and settings
  int currentMenu = 0;
  int currentSubmenu = 0;
}

void setupMenus() {
${generateMenuDefinitions(menuItems)}
}

void handleMenus() {
  // Menu navigation logic
  if (event_press(${menuSettings?.binding || "PS4_SHARE"})) {
    currentMenu = (currentMenu + 1) % ${menuItems.length};
  }
}
`;

// Append menu position settings as comments or commands
if (menuSettings) {
  script += `\n// Menu Settings\n`;
  script += `// Position: ${menuSettings.position}\n`;
  if (menuSettings.position === "Custom" && menuSettings.customX !== undefined && menuSettings.customY !== undefined) {
    script += `// Custom Position: X=${menuSettings.customX}, Y=${menuSettings.customY}\n`;
  }
}

// Append enhancement settings as comments or commands
if (enhancementSettings) {
  script += `\n// Enhancement Settings\n`;
  script += `SET_VERTICAL_RECOIL(${enhancementSettings.verticalRecoil});\n`;
  script += `SET_HORIZONTAL_RECOIL(${enhancementSettings.horizontalRecoil});\n`;
  if (enhancementSettings.aimEnhancerMode !== "None") {
    script += `SET_AIM_ENHANCER("${enhancementSettings.aimEnhancerMode}");\n`;
  }
}

  return script;
}

function generateMenuDefinitions(items: MenuItem[], depth = 1): string {
  let result = "";
  const indent = "  ".repeat(depth);

  items.forEach((item, index) => {
    result += `${indent}// Menu: ${item.name}\n`;
    result += `${indent}MENU "${item.name}" {\n`;
    
    if (item.children && item.children.length > 0) {
      result += generateMenuDefinitions(item.children, depth + 1);
    } else {
      result += `${indent}  // Add your menu actions here\n`;
      result += `${indent}  if (event_press(PS4_CROSS)) {\n`;
      result += `${indent}    // Action for ${item.name}\n`;
      result += `${indent}  }\n`;
    }
    
    result += `${indent}}\n\n`;
  });

  return result;
}
