"use client";

import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion";
import { useState } from "react";

interface MenuItem {
  id: string;
  name: string;
  type: "menu" | "submenu";
  children?: MenuItem[];
}

interface ZenMenuItemProps {
  item: MenuItem;
  onUpdate: (updatedItem: MenuItem) => void;
  onDelete: (id: string) => void;
  depth?: number;
}

export function ZenMenuItem({ item, onUpdate, onDelete, depth = 0 }: ZenMenuItemProps) {
  const [isExpanded, setIsExpanded] = useState(false);

  const addSubmenu = () => {
    const newSubmenu: MenuItem = {
      id: Math.random().toString(36).substr(2, 9),
      name: "New Submenu Item",
      type: "submenu",
      children: [],
    };

    const updatedItem = {
      ...item,
      children: [...(item.children || []), newSubmenu],
    };

    onUpdate(updatedItem);
  };

  const updateSubmenuItem = (submenuItem: MenuItem) => {
    if (!item.children) return;

    const updatedChildren = item.children.map((child) =>
      child.id === submenuItem.id ? submenuItem : child
    );

    onUpdate({
      ...item,
      children: updatedChildren,
    });
  };

  const deleteSubmenuItem = (submenuId: string) => {
    if (!item.children) return;

    const updatedChildren = item.children.filter((child) => child.id !== submenuId);

    onUpdate({
      ...item,
      children: updatedChildren,
    });
  };

  return (
    <Card className={`mb-2 p-4 ${depth > 0 ? "ml-6" : ""}`}>
      <div className="flex items-center gap-2">
        <input
          type="text"
          value={item.name}
          onChange={(e) => onUpdate({ ...item, name: e.target.value })}
          className="flex-1 rounded-md bg-zinc-800 px-3 py-2 text-white"
          placeholder="Enter menu item name"
        />
        <Button onClick={addSubmenu} variant="secondary" size="sm">
          Add Submenu
        </Button>
        <Button onClick={() => onDelete(item.id)} variant="destructive" size="sm">
          Delete
        </Button>
      </div>

      {item.children && item.children.length > 0 && (
        <Accordion type="single" collapsible className="mt-2">
          <AccordionItem value="submenu">
            <AccordionTrigger onClick={() => setIsExpanded(!isExpanded)}>
              Submenus ({item.children.length})
            </AccordionTrigger>
            <AccordionContent>
              {item.children.map((submenu) => (
                <ZenMenuItem
                  key={submenu.id}
                  item={submenu}
                  onUpdate={updateSubmenuItem}
                  onDelete={deleteSubmenuItem}
                  depth={depth + 1}
                />
              ))}
            </AccordionContent>
          </AccordionItem>
        </Accordion>
      )}
    </Card>
  );
}
