"use client";

import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { useState, useEffect } from "react";
import { ScrollArea } from "@/components/ui/scroll-area";
import { ZenMenuItem } from "@/components/ZenMenuItem";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";
import { generateCronusZenScript } from "@/lib/scriptGenerator";
import { ShortcutGuide } from "@/components/ShortcutGuide";
import { MenuSettings } from "@/components/MenuSettings";

interface MenuItem {
  id: string;
  name: string;
  type: "menu" | "submenu";
  children?: MenuItem[];
}

interface MenuSettings {
  position: string;
  binding: string;
  customX?: number;
  customY?: number;
}

export default function Home() {
  const [menuItems, setMenuItems] = useState<MenuItem[]>([]);
  const [activeTab, setActiveTab] = useState("designer");
  const [generatedScript, setGeneratedScript] = useState("");
  const [menuSettings, setMenuSettings] = useState<MenuSettings>({
    position: "Center",
    binding: "PS4_Share",
  });

  const [enhancementSettings, setEnhancementSettings] = useState({
    verticalRecoil: 0,
    horizontalRecoil: 0,
    aimEnhancerMode: "None",
  });

  const addMenuItem = () => {
    const newItem: MenuItem = {
      id: Math.random().toString(36).substr(2, 9),
      name: "New Menu Item",
      type: "menu",
      children: [],
    };
    setMenuItems([...menuItems, newItem]);
  };

  const updateMenuItem = (updatedItem: MenuItem) => {
    setMenuItems(
      menuItems.map((item) => (item.id === updatedItem.id ? updatedItem : item))
    );
  };

  const [itemToDelete, setItemToDelete] = useState<string | null>(null);

  const deleteMenuItem = (id: string) => {
    setItemToDelete(id);
  };

  const confirmDelete = () => {
    if (itemToDelete) {
      setMenuItems(menuItems.filter((item) => item.id !== itemToDelete));
      setItemToDelete(null);
    }
  };

  const generateScript = () => {
    const script = generateCronusZenScript(menuItems, menuSettings);
    setGeneratedScript(script);
  };

  const downloadScript = () => {
    const blob = new Blob([generatedScript], { type: "text/plain" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = "cronus-zen-script.txt";
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  // Handle keyboard shortcuts
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.ctrlKey) {
        switch (e.key.toLowerCase()) {
          case 'n':
            e.preventDefault();
            addMenuItem();
            break;
          case 'g':
            e.preventDefault();
            generateScript();
            break;
          case 's':
            e.preventDefault();
            if (generatedScript) {
              downloadScript();
            }
            break;
          case 'delete':
            e.preventDefault();
            if (menuItems.length > 0) {
              deleteMenuItem(menuItems[menuItems.length - 1].id);
            }
            break;
        }
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [generatedScript, menuItems, addMenuItem, generateScript, downloadScript, deleteMenuItem]);

  return (
    <main className="min-h-screen bg-gradient-to-b from-zinc-900 to-black p-4 md:p-8">
      <div className="mx-auto max-w-6xl relative">
        <AlertDialog open={!!itemToDelete} onOpenChange={() => setItemToDelete(null)}>
          <AlertDialogContent className="fixed top-[50%] left-[50%] translate-x-[-50%] translate-y-[-50%]">
            <AlertDialogHeader>
              <AlertDialogTitle>Delete Menu Item</AlertDialogTitle>
              <AlertDialogDescription>
                Are you sure you want to delete this menu item? This action cannot be undone.
              </AlertDialogDescription>
            </AlertDialogHeader>
            <AlertDialogFooter>
              <AlertDialogCancel>Cancel</AlertDialogCancel>
              <AlertDialogAction onClick={confirmDelete} className="bg-red-600 hover:bg-red-700">
                Delete
              </AlertDialogAction>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>
        <div className="mb-8 text-center">
          <h1 className="mb-2 text-4xl font-bold text-white">Cronus Zen Script Designer</h1>
          <p className="text-zinc-400 mb-4">Create custom menus and scripts for your Cronus Zen device</p>
          <ShortcutGuide />
        </div>
        <MenuSettings onSettingsChange={setMenuSettings} />

        <Tabs defaultValue="designer" className="w-full" onValueChange={setActiveTab}>
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="designer">Menu Designer</TabsTrigger>
            <TabsTrigger value="preview">Script Preview</TabsTrigger>
            <TabsTrigger value="enhancements">Enhancements</TabsTrigger>
          </TabsList>

          <TabsContent value="designer">
            <Card className="p-6">
              <div className="mb-4 space-y-4">
                <div className="flex items-center justify-between">
                  <div>
                    <h2 className="text-2xl font-semibold">Menu Structure</h2>
                    <p className="text-sm text-zinc-400">Design your menu hierarchy</p>
                  </div>
                  <TooltipProvider>
                    <Tooltip>
                      <TooltipTrigger asChild>
                        <AlertDialog>
                          <AlertDialogTrigger asChild>
                            <Button className="bg-zinc-800 hover:bg-zinc-700 text-white">
                              Add Menu Item
                            </Button>
                          </AlertDialogTrigger>
                          <AlertDialogContent className="fixed top-[50%] left-[50%] translate-x-[-50%] translate-y-[-50%]">
                            <AlertDialogHeader>
                              <AlertDialogTitle>Add New Menu Item</AlertDialogTitle>
                              <AlertDialogDescription>
                                This will add a new menu item to your script. You can add submenus to it later.
                              </AlertDialogDescription>
                            </AlertDialogHeader>
                            <AlertDialogFooter>
                              <AlertDialogCancel>Cancel</AlertDialogCancel>
                              <AlertDialogAction onClick={addMenuItem}>Add Item</AlertDialogAction>
                            </AlertDialogFooter>
                          </AlertDialogContent>
                        </AlertDialog>
                      </TooltipTrigger>
                      <TooltipContent>
                        <p>Create a new menu item and customize it with submenus</p>
                      </TooltipContent>
                    </Tooltip>
                  </TooltipProvider>
                </div>
              </div>

              <ScrollArea className="h-[500px] rounded-md border p-4">
                {menuItems.length === 0 ? (
                  <div className="flex h-full items-center justify-center text-zinc-400">
                    No menu items yet. Click "Add Menu Item" to get started.
                  </div>
                ) : (
                  menuItems.map((item) => (
                    <ZenMenuItem
                      key={item.id}
                      item={item}
                      onUpdate={updateMenuItem}
                      onDelete={deleteMenuItem}
                    />
                  ))
                )}
              </ScrollArea>
            </Card>
          </TabsContent>

          <TabsContent value="preview">
            <Card className="p-6">
              <div className="mb-4 flex items-center justify-between">
                <div>
                  <h2 className="text-2xl font-semibold">Generated Script</h2>
                  <p className="text-sm text-zinc-400">Preview and download your script</p>
                </div>
                <div className="flex items-center gap-2">
                  <TooltipProvider>
                    <Tooltip>
                      <TooltipTrigger asChild>
                        <Button onClick={generateScript} variant="secondary">
                          Generate Script
                        </Button>
                      </TooltipTrigger>
                      <TooltipContent>
                        <p>Generate Cronus Zen compatible script from your menu structure</p>
                      </TooltipContent>
                    </Tooltip>
                  </TooltipProvider>
                  
                  <TooltipProvider>
                    <Tooltip>
                      <TooltipTrigger asChild>
                        <Button onClick={downloadScript} disabled={!generatedScript}>
                          Download Script
                        </Button>
                      </TooltipTrigger>
                      <TooltipContent>
                        <p>Download the generated script as a text file</p>
                      </TooltipContent>
                    </Tooltip>
                  </TooltipProvider>
                </div>
              </div>

              <ScrollArea className="h-[500px] rounded-md border bg-zinc-900 p-4">
                {!generatedScript ? (
                  <div className="flex h-full items-center justify-center text-zinc-400">
                    Click "Generate Script" to preview your Cronus Zen script.
                  </div>
                ) : (
                  <pre className="text-sm text-white">{generatedScript}</pre>
                )}
              </ScrollArea>
            </Card>
          </TabsContent>

          <TabsContent value="enhancements">
            <Card className="p-6">
              <EnhancementSettings onSettingsChange={setEnhancementSettings} />
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </main>
  );
}
